<template>
  <div class="l-orderlist">
    <van-nav-bar 
      title="我的订单"
      left-arrow 
      @click-left="onClickLeft" 
      class="c-nav-bar"
    >
    </van-nav-bar>
      <div class="l-orderListBody">
        <van-tabs v-model="active" class="l-ol-tabs">
          <van-tab title="未支付">
            <ul class="l-ol-itembox">
              <li v-for="items in unPayList" :key="items.orderno" class="l-order-list">
                <div class="l-ol-header">
                  <span>订单编号:</span>&nbsp;<span>{{items.orderno}}</span>
                </div>
                <ul>
                  <li v-for="item in items.goods" class="l-list" :key="item.goodsid">
                    <div class="l-img"><img :src="item.pic_url" @error.once="setDefImg"/></div>
                    <div class="l-imgInfo">
                      <div class="l-img-title">
                        <span>{{item.name}}</span>
                        <span class="l-level"><img :src="require('../../assets/icon/level' + item.goodsid +'.jpg')"></span>
                      </div>
                      <div class="l-img-price">
                        <span>价格:</span><span class="l-img-marginL">{{item.price}}</span><span>xtoken</span>
                      </div>
                      <div class="l-img-memo">
                        <span>产能:</span><span class="l-img-marginL">{{item.memo}}</span>
                      </div>
                    </div>
                    <div class="l-orderNum">
                      数量：<span>{{item.count}}</span>
                    </div>
                    <!-- {{item.name}} -->
                  </li>
                </ul>
                <van-cell-group class="l-ol-desc">
                    <van-cell title="订单时间" :value="items.createtime" />
                    <van-cell title="总价(xtoken)" :value="items.coin | toNumberString" />
                </van-cell-group>                 
                <div class="l-unpay">
                  <van-button type="default" size="small" @click="cancelPay(items.orderno)">取消支付</van-button>
                  <van-button type="warning" size="small"  @click="onPay(items.coin,items.orderno)" >支付</van-button>
                </div>
              </li>
            </ul>

          </van-tab>
          <van-tab title="已完成">
            <ul class="l-ol-itembox">
              <li v-for="items in payList" :key="items.orderno" class="l-order-list">
                <div class="l-ol-header">
                  <span>订单编号:</span>&nbsp;<span>{{items.orderno}}</span>
                </div>
                <ul>
                  <li v-for="item in items.goods" class="l-list" :key="item.goodsid">
                    <div class="l-img"><img :src="item.pic_url" @error.once="setDefImg"/></div>
                    <div class="l-imgInfo">
                      <div class="l-img-title">
                        <span>{{item.name}}</span>
                        <span class="l-level"><img :src="require('../../assets/icon/level' + item.goodsid +'.jpg')"></span>
                      </div>
                      <div class="l-img-price">
                        <span>价格:</span><span class="l-img-marginL">{{item.price}}</span><span>xtoken</span>
                      </div>
                      <div class="l-img-memo">
                        <span>产能:</span><span class="l-img-marginL">{{item.memo}}</span>
                      </div>
                    </div>
                    <div class="l-orderNum">
                      数量：<span>{{item.count}}</span>
                    </div>
                    <!-- {{item.name}} -->
                  </li>
                </ul>
                <van-cell-group class="l-ol-desc">
                    <van-cell title="订单时间" :value="items.createtime" />
                    <van-cell title="总价(xtoken)" :value="items.coin | toNumberString" />
                </van-cell-group>                 
              </li>
            </ul>           
          </van-tab>
        </van-tabs>
      </div>
    <page-loading v-show="loading"></page-loading>
    <van-actionsheet v-model="payShow" title="请输入密码"  @cancel="onClosePaybox">
      <Pay @finish="onPwdFinish" :num="total"></Pay>
    </van-actionsheet>   
  </div>
</template>
<script>
import '../../common/common.scss'
import Pay from '@/components/Pay'
import util from '../../common/util'

export default {
  name: 'orderlist',
   components: {
    Pay
  },
  data() {
    return {
      loading: false,
      active: 0,
      total: 10,
      payShow: false,
      amount:'',
      errorImg: require('../../assets/pic/error-img.jpg'),
      unPayList: [],
      payList: []
    };
  },

  methods: {
    onClickLeft () {
      this.$router.back();
    },
    setDefImg(e) {
      e.target.src = this.errorImg;
    },
    getPayList() {
      this.loading = true;
      this.$http.post("/order/getPayDealOrder", {
        "did": window.device.uuid,   //商品ID  
      }).then((res) => {
        this.payList = this.convertTime(res);
      }).finally(()=>{
        this.loading = false;
      })
    },
    getUnPayList() {
      this.loading = true;
      this.$http.post("/order/getUnPayOrder", {
        "did": window.device.uuid,   //商品ID  
      }).then((res) => {
        // console.log(res)
        this.unPayList = this.convertTime(res);
      }).finally(()=>{
        this.loading = false;
      })
    },
    cancelPay(n){
      this.$dialog.confirm({
        title: '提示',
        message: '确定要取消该订单?'
      }).then(() => {
        this.doCancelPay(n);
      }).catch(() => {
        console.log("cancel");
      });
    },
    doCancelPay(n){
      this.loading = true;
      this.$http.post("order/cancelUnPayOrder", {
        "did": window.device.uuid,
        "orderno": n
      }).then(res=>{
        this.getUnPayList();
      }).finally(()=>{
        this.loading = false;
      })
    },
    onClosePaybox() {
      this.orderNum = null;
    },
    onPwdFinish(v) {
      this.loading = true;
      this.$http.post("order/confirmUnPayOrder", {
        "paypwd": v,
        "did": window.device.uuid,
        "orderno": this.orderNum
      }).then((res) => {
        this.$router.push("/shop/success");
      }).catch((e)=> {
        let code = util.getErrorCode(e);
        let theCode = window.globalData.code;
        if(code == theCode.CART_COIN_NOT_ENOUGH) {
          this.$dialog.alert({
            message: '您的余额不足,请充值后到未支付订单页面完成支付'
          }).then(()=> {
            this.$router.push("/shop/orderlist");
          });
        }
        else if(code == theCode.PAYPASSWORD_IS_WRONG) {
          this.$dialog.alert({message: '密码错误'})
        }
        else if(code == theCode.CART_IS_EMPTY) {
          this.$dialog.alert({message: '购物车是空的'}) 
        }
        else {
          this.$dialog.alert({message: '操作失败'}) 
        }
      }).finally(()=>{
        this.loading = false;
      })     
    },
    onPay(t, orderNum) {
      this.payShow = true;
      this.total = t;
      this.orderNum = orderNum;
    },
    getData() {
      this.getUnPayList()
      this.getPayList()
    },
    convertTime(arr){
      return arr.map(item=>{
        item.createtime = util.formatTime(new Date(item.createtime*1000), 'yyyy-MM-dd HH:mm:ss')
        return item;
      })
    }
  },
  mounted () {
    this.active = this.$route.query.tabIndex || 0;
    this.getData();
  }
}
</script>

